$url:"http://shopping.dangdang.com/shoppingcart/images/icon_sprite.png";
#hd{
    font-size: 12px;
    color: #646464;
    min-width: 960px;
    background-color: #fff;
    a:hover{
        color: #ff2832;
    }
    .tools{
        width: 100%;
        background-color: #f9f9f9;
        border-bottom: 1px solid #f2f2f2;
        height: 34px;
        line-height: 34px;
        z-index: 10010;
        position: relative;
        .tools_con{
            width: 960px;
            margin: 0 auto;
            height: 34px;
        }
    }
}
.tools_con{
    .welcome{
        padding: 5px 0 0 10px;
        height: 24px;
        line-height: 24px;
        position: relative;
        float: left;
        z-index: 50;
        a{
            color: #ff2832;
            display: inline-block;
            margin-right: 4px;
        }
    }
    .toolsnav{
        float: right;
        padding-top: 2px;
        li{
            float: left;
            height: 22px;
            padding-top: 4px;
            line-height: 21px;
            background: url(http://img32.ddimg.cn/upload_img/00363/header/home_sprite.png) no-repeat left -285px;
            position: relative;
            z-index: 100;
            a{
                display: block;
                position: relative;
                line-height: 21px;
                padding: 0 7px 0 8px;
                color: #646464;
            }
            i{
                float: left;
                width: 15px;
                height: 14px;
                background: url(http://img32.ddimg.cn/upload_img/00363/header/home_sprite.png) no-repeat -40px -86px;
                overflow: hidden;
                margin: 3px 9px 0 0;
            }
            b{
                color: #ff2832;
                font-style: 12px;
            }
        }
        li:nth-of-type(1){
            background-image: none;
        }
    }
}

.logo_line{
    width: 100%;
    border-bottom: 4px solid #ff2832;
    height: 100px;
    .logo_linecon{
        width: 960px;
        margin: 0 auto;
        .logo{
            float: left;
            padding: 20px 0 0 15px;
        }
        .shopping{
            float: right;
            width: 634px;
            height: 36px;
            background: url(http://shopping.dangdang.com/shoppingcart/images/procedure_bg.png) 0 0 no-repeat;
            font: bold 14px/36px "Microsoft Yahei";
            color: #646464;
            margin: 30px 0 0;
        }
    }
}
.logo_linecon .shopping{
    .current{
        color: #fff;
        width: 206px;
    }
    span{
        width: 208px;
        display: inline-block;
        text-align: center;
    }
}

.add_line{
    width: 100%;
    background-color: #fafafa;
    .add_linecon{
        width: 960px;
        margin: 0 auto;
    }
    .address{
        height: 22px;
        line-height: 22px;
        margin: 15px 0;
        background-color: #fcfcfc;
        border: 1px solid #ebebeb;
        padding: 0 15px;
        border-radius: 12px;
        display: inline-block;
        .select_address{
            position: relative;
            display: inline-block;
            z-index: 1003;
        }
    }
    .address>span{
        width: 22px;
        height: 22px;
        background: url($url) 0 5px no-repeat;
        display: inline-block;
        margin-right: 5px;
    }
}
.select_address{
    .text{
        color: #646464;
        border: 1px solid #ebebeb;
        border-width: 0 1px;
        height: 22px;
        line-height: 22px;
        padding: 0 14px 0 5px;
        background: url($url) no-repeat right -53px #fff;
        cursor: pointer;
        position: relative;
        z-index: 88;
        display: block;
    }
    .area_pop{
        position: absolute;
        width: 369px;
        border: 1px solid #ebebeb;
        background-color: #fff;
        z-index: 1003;
        padding: 0 2px;
        font-size: 12px;
        top: 22px;
        left: 0;
    }
}
.area_pop{
    .btn{
        display: block;
        width: 11px;
        height: 11px;
        background: url($url) no-repeat -48px 0;
        overflow: auto;
        font-size: 0;
        position: absolute;
        right: 8px;
        top: 8px;
        color: #323232;
    }
    .tab{
        height: 27px;
        border-bottom: 1px solid #ebebeb;
        li{
            height: 20px;
            float: left;
            margin: 7px 0 -1px 7px;
            padding: 0 6px;
            background: #f1f1f1 url($url) no-repeat right -91px;
        }
        li a,li a:hover{
            font-size: 14px;
            line-height: 20px;
            color: #464646;
            display: block;
            margin-right: 12px;
            text-decoration: none;
            max-width: 100px;
            overflow: hidden;
            white-space: nowrap;
        }
        .choose{
            height: 20px;
            padding: 0 6px;
            border: 1px solid #ebebeb;
            border-bottom: 0;
            background-color: #fff;
        }
    }
    .list{
        padding: 4px 0 8px 17px;
        min-height: 88px;
        display: none;
        li{
            float: left;
            padding-left: 3px;
            line-height: 22px;
            width: 85px;
            height: 22px;
            white-space: nowrap;
        }
    }
    .show{
        display: block;
    }
}

#cart{
    width: 960px;
    margin: 0 auto;
    position: relative;
    .cart_title{
        line-height: 36px;
        height: 36px;
        margin-top: 10px;
        li{
            float: left;
        }
        .li1{
            text-align: left;
            width: 202px;
        }
        .li2{
            text-align: left;
            width: 280px;
        }
        .li3,.li4,.li5{
            text-align: center;
            width: 130px;
        }
        .li6{
            text-align: center;
            width: 70px;
        }
        li input{
            display: block;
            float: left;
            margin-top: 12px;
            margin-right: 10px;
        }
    }
    .cart_content{
        .shopping_list{
            width: 958px;
            border: 1px solid #ebebeb;
            background-color: #fafafa;
            margin-bottom: 40px;
            tr{
                width: 100%;
                border-bottom: 1px solid #e4e4e4;
            }
            td{
                vertical-align: top;
                padding-top: 15px;
                // position: relative;
                line-height: 22px;
                text-align: center;
                // border-bottom: 1px solid #e4e4e4;
            }
        }
    }
}
.shopping_list{
    .row1{
        padding: 0 0 0 10px;
        width: 42px;
        vertical-align: middle;
        border-bottom: 0;
    }
    .row2{
        background: url(http://shopping.dangdang.com/shoppingcart/images/tree.png) 40px 0 no-repeat;
        padding: 20px 20px 20px 0;
        width: 80px;
        border-bottom: 0;
        position: relative;
        z-index: 9;
        font-size: 0;
        line-height: 0;
        a img{
            display: block;
            width: 80px;
            height: 80px;
        }
        .big_img{
            display: none;
            padding: 9px;
            position: absolute;
            z-index: 100;
            width: 200px;
            height: 200px;
            border: 1px solid #ccc;
            background-color: #fff;
            left: 100px;
            top: 0;
            img{
                display: block;
                width: 200px;
                height: 200px;
            }
        }
        .arrow{
            width: 8px;
            height: 15px;
            display: block;
            background: url($url) -18px -250px no-repeat;
            position: absolute;
            top: 52px;
            left: -7px;
            z-index: 100;
        }
    }
    .row2:hover .big_img{
        display: block;
    }
    .row3{
        width: 330px;
        line-height: 18px !important;
        text-align: left !important;
        position: relative;
        padding-top: 17px;
        .name{
            width: 100%;
            height: 36px;
            overflow: hidden;
            margin-bottom: 27px;
            word-break: break-all;
            word-wrap: break-word;
        }
        .name a:hover{
            color: #ff2832;
            text-decoration: underline;
        }
    }
    .row4,.row5,.row6{
        width: 130px;
    }
    .row5{
        // width: 130px;
        .amount{
            display: block;
            height: 28px;
            width: 94px;
            border: 1px solid #dcdcdc;
            line-height: 28px;
            font-size: 14px;
            margin: 5px auto;
            overflow: hidden;
            a{
                display: block;
                float: left;
                height: 28px;
                width: 28px;
                background-color: #f4f4f4;
            }
            a:link, a:visited {
                text-decoration: none;
                color: #323232;
            }
            input{
                width: 36px;
                height: 28px;
                float: left;
                padding: 0;
                text-align: center;
                border: 1px solid #dcdcdc;
                border-width: 0 1px;
                background-color: #fff;
                font-weight: bold;
            }
        }
    }
    .row6{
        // width: 130px;
        color: #ff2832;
    }
    .row7{
        cursor: pointer;
        position: relative;
        width: 70px;
        #delete:hover{
            color: #ff2832;
            text-decoration: underline;
        }
    }
}
tfoot .total{
    height: 53px;
    line-height: 53px;
    .td1{
        text-align: center;
        color: #323232;
        font-weight: bold;
    }
    .td2{
        text-align: left;
    }
    .td3{
        text-align: center;
        font-size: 16px;
        color: #ff2832;
    }
}

.car_fixed{
    position: fixed;
    bottom: -20px;
    z-index: 101;
    width: 100%;
    left: 0px;
    color: #969696;
    .shopping_total{
        width: 960px;
        height: 72px;
        z-index: 10;
        margin: 20px auto;
        padding: 0 62px;
        background: url(http://shopping.dangdang.com/shoppingcart/images/shopping_total.png) 0 center no-repeat;
        position: relative;
    }
    .shopping_totalleft{
        float: left;
        line-height: 58px;
        width: 430px;
        position: relative;
        .tanchukuang{
            @extend .tanchukuang;
            left: 5px;
        }
        input{
            display: block;
            float: left;
            margin-top: 24px;
            margin-right: 5px;
        }
        span{
            margin-left: 28px;
            b{
                color: #ff2832;
                font-weight: normal;
            }
        }
        .moredel{
            color: #323232;
            cursor: pointer;
        }
        .moredel:hover{
            color: #ff2832;
            text-decoration: underline;
        }
    }
    .shopping_totalright{
        float: right;
        margin: 18px 0 0;
        width: 530px;
        position: relative;
        .subtotal{
            padding-right: 30px;
            line-height: 24px;
            float: right;
        }
        .total_btn{
            float: right;
            display: block;
            width: 116px;
            height: 38px;
            font: bold 18px/38px "Microsoft Yahei";
            color: #fff;
            background-color: #ff2832;
            border-radius: 2px;
            text-align: center;
            margin-top: 4px;
        }
        .unable{
            cursor: default;
            background-color: #e1e1e1;
        }
    }
}
.subtotal{
    .cartsum{
        width: 114px;
        text-align: right;
        padding: 0 8px 0 0;
    }
    .price{
        font-size: 18px;
        color: #ff2832;
    }
}

.tuijian{
    width: 960px;
    margin: 0 auto;
    .tuijian_con{
        width: 100%;
        height: 580px;
        box-sizing: border-box;
        border: 1px solid #dcdcdc;
        margin-bottom: 30px;
        h2{
            line-height: 30px;
            margin: 10px;
            margin-left: 20px;
        }
    }
    li{
        width: 150px;
        border: none 0;
        padding: 0 10px 12px;
        margin: 0 20px 0 0;
        text-align: center;
        float: left;
        color: #323232;
        line-height: 16px;
        position: relative;
        overflow: hidden;
        a{
            display: inline-block;
            width: 150px;
            height: 150px;
            margin: 10px 0;
        }
        p{
            text-align: left;
            line-height: 14px;
            width: 150px;
            height: 28px;
            overflow: hidden;
            span{
                font-size: 16px;
                color: #ff2832;
                line-height: 32px;
            }
        }
        img{
            display: block;
            width: 150px;
            height: 150px;
        }
    }
}

.footer{
    border-top: 2px solid #fa5000;
    margin-top: 15px;
    background-color: #fff;
    clear: both;
    .footer_box{
        color: #8c8c8c;
        font-size: 12px;
        border-top: 1px solid #ebebeb;
        width: 100%;
        min-width: 960px;
        padding: 17px 0 50px;
        .footer_copyright{
            line-height: 20px;
            text-align: center;
            width: 960px;
            margin: 0 auto;
            padding: 10px 0 0 0;
            color: #8c8c8c;
            img{
                vertical-align: bottom;
            }
        }
    }
}

.tanchukuang{
    position: absolute;
    right: -35px;
    top: -105px;
    width: 200px;
    height: 120px;
    z-index: 99;
    padding: 0 0 0 10px;
    border: 1px solid #ccc;
    background: url('http://shopping.dangdang.com/shoppingcart/images/pop_del_bg.png') right bottom no-repeat #fff;
    text-align: center;
    .success{
        font-size: 14px;
        color: #323232;
        line-height: 18px;
        padding: 25px 0;
    }
    .org_btnb:hover{
        color: #ff2832;
    }
    .org_btnb{
        width: 50px;
        height: 22px;
        line-height: 22px;
        display: inline-block;
        border: 1px solid #e6e6e6;
        margin: 5px 10px 0 0!important;
        border-radius: 2px;
        background-color: #f8f8f8;
    }
    .promo_btn{
        @extend .org_btnb;
    }
}